import PropTypes from "prop-types";
import { useState } from "react";
import styles from "./Projects.module.css";
import ReactPlayer from "react-player";
// import styled from "styled-components";

// let BgWrapper = styled.div`
//     background: url("../img/bg_human.jpg");
// `;

function Projects({img, title, summary, using, bd}){
    const [hover, setHover] = useState(false);
    // console.log(styles["tooltiptext"]);
    return(
        <div className={styles.project}
            onMouseEnter={()=>{
                setHover(true);
                // document.getElementById(title).play();
            }}
            onMouseLeave={()=>{
                setHover(false);
                // document.getElementById(title).pause();
            }}
            // onMouseMove={(e)=>{
                
            // }}
        >
            {(bd !== undefined) ? 
                hover ? 
                <div className={styles.project__img}>
                    <ReactPlayer
                        className="player"
                        url={"https://youtu.be/ijwl1JguLRc"}
                        controls
                        playing={true}
                        loop={true}
                        width="525px"
                        heigth="50px"
                    /> 
                </div>
                : <img src={img} alt={title} className={styles.project__img}/>
                : <img src={img} alt={title} className={styles.project__img}/>
            }            
            {/* <img src={img} alt={title} className={styles.project__img}/> */}
            <div>
                <h2 className={styles.project__title}>{title}</h2>
                <p>{(summary.length > 100 && !hover) ? `${summary.slice(0,100)}...` : summary}</p>
                <ul className={styles.project__using}>
                {using.map((g) => (
                    <li key={g}>{g}</li>
                ))}
                </ul>
                
                {/* {(bd !== undefined) ? 
                    hover ? 
                    <span className={styles["tooltiptext"]}>
                        <div className="pointer">
                            <ReactPlayer
                                className="player"
                                url={"https://youtu.be/ijwl1JguLRc"}
                                controls
                                playing={true}
                                loop={true}
                                width="700px"
                                heigth="700px"
                            /> 
                        </div>
                    </span> 
                    : <span> no hover </span>
                    : <span className={styles["tooltiptext"]}>
                        no video
                    </span>
                } */}
            </div>
        </div>
    );
}

Projects.propTypes={
    img: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    using: PropTypes.arrayOf(PropTypes.string).isRequired,
    bd: PropTypes.string
}

export default Projects;